.card {
  background: #fff;
  border-radius: 2px;
  height: 150px;
  margin: 1rem;
  position: relative;
  width: 150px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
  
}
.card > .img-container > img {
  width: 100%;
  height:100%;
  cursor: pointer;
}

.wrapper {
  box-sizing: border-box;
  margin: 0 auto;
  /* padding-top: 50px; */
  background:navy;
  height: 100%;
  width:60%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  height: 4rem;
  text-align: center;
  font-size: 2rem;
  color:white;
  margin-bottom: 20px;
  background:navy;
  padding-top: 10px;
  font: fantasy;
}

* {
  box-sizing: border-box;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
.App {
  text-align: center;
   background-image: url(/clickyGame/static/media/Lines-PNG-Transparent-Background.4f3b706a.png); 
   margin-bottom: 10px;
}
 .App-intro {
  font-size: large;
  padding: 20px;
  color: rgb(57, 57, 177);
}
.score {
  color:green;
  font-weight: bolder;

}

